import React, { useState } from 'react'
import SearchBar from "../components/SearchBar";
import { MapContainer, GeoJSON, TileLayer, ImageOverlay } from "react-leaflet";
import * as L from "leaflet";
import "leaflet/dist/leaflet.css";
import "leaflet-fullscreen/dist/Leaflet.fullscreen.js";
import "leaflet-fullscreen/dist/leaflet.fullscreen.css";
import { BaseMapsLayers, mapCenter, maxBounds, setDragging, setInitialMapZoom } from '../helpers/mapFunction'
import BaseMap from '../components/Basemap'
import IndiaBoundary from '../assets/data/shapefiles/IndiaBoundary.json';
import IndiaDistricts from '../assets/data/shapefiles/IndiaDistricts.json';
import IndiaStates from '../assets/data/shapefiles/IndiaStates.json';
import IndiaSubdistricts from '../assets/data/shapefiles/IndiaSubdistricts.json';
import ShortFooter from '../components/ShortFooter';
import MonsoonData from "../assets/data/MonsoonData.json"
import { Autocomplete, TextField } from "@mui/material";
import { Box } from "@mui/system";
import MapLegend from '../components/MapLegend';
import IndiaClimateZones from "../assets/data/IndiaClimateZones_1991_2020.png"
import climate_zones_legend from "../assets/images/climate_zones_legend.jpg"

// const LayersOptions = [
//     { name: 'Irrigated/Rainfed', value: 'Irrigated_Rainfed' },
//     { name: 'Elevation', value: 'elevation' },
// ];

const VectorDataOptions = [
    { name: 'Country Boundry', value: 'Country_Boundry', data: IndiaBoundary },
    { name: 'States Boundry', value: 'Basin_Boundry', data: IndiaStates },
    { name: 'District Boundry', value: 'District_Boundry', data: IndiaDistricts },
    { name: 'Sub-district Boundry', value: 'SubDistrict_Boundary', data: IndiaSubdistricts },

];

const MonsoonDataOptions = [
    {
        name: "Climate Zones (1991-2020)",
        value: "climate_zones_map",
    },
    {
        name: "June Percentage Anomaly",
        value: "june_panomaly_mean",
    },
    {
        name: "July Percentage Anomaly",
        value: "july_panomaly_mean",
    },
    {
        name: "August Percentage Anomaly",
        value: "august_panomaly_mean",
    },
    {
        name: "September Percentage Anomaly",
        value: "september_panomaly_mean",
    },
    {
        name: "JJAS Percentage Anomaly",
        value: "jjas_percent_anomaly_mean",
    },
    {
        name: "October Percentage Anomaly",
        value: "october_panomaly_mean",
    },
    {
        name: "November Percentage Anomaly",
        value: "november_panomaly_mean",
    },
    {
        name: "December Percentage Anomaly",
        value: "december_panomaly_mean",
    },
    {
        name: "OND Percentage Anomaly",
        value: "ond_panomaly_mean",
    },

]




const ClimateRiskDashboard = () => {
    const [selectedRasterLayer, setSelectedRasterLayer] = useState("");
    const [rasterLayerOpacity, setRasterLayerOpacity] = useState(1);
    const [selectedVectorData, setSelectedVectorData] = useState(VectorDataOptions[0]);
    const [selectedBasemapLayer, setSelectedBasemapLayer] = useState(BaseMapsLayers[0]);

    const [selectedMapData, setSelectedMapData] = useState(null);

    console.log(selectedMapData)
    const handleDataSelectionChange = (e) => {
        const selectedItem = MonsoonDataOptions.find((item) => item.value === e.target.value);
        setSelectedMapData(selectedItem);
    };


    const handleBasemapSelection = (e) => {
        const selectedItem = BaseMapsLayers.find((item) => item.name === e.target.value);
        setSelectedBasemapLayer(selectedItem);


    };

    console.log(selectedVectorData)




    const handleRasterLayerSelection = (e) => {
        const value = e.target.value;
        setSelectedRasterLayer((prevSelectedRaster) =>
            prevSelectedRaster === value ? '' : value
        );
    };

    const handleOpacityChange = (e) => {
        setRasterLayerOpacity(parseFloat(e.target.value));
    };


    const handleVectorLayerSelection = (e) => {
        const selectedItem = VectorDataOptions.find((item) => item.value === e.target.value);
        setSelectedVectorData(selectedItem);
        //     setSelectedVectorData((prevSelectedRaster) =>
        //     prevSelectedRaster === selectedItem ? null : selectedItem
        // );

    };




    function TalukaOnEachfeature(feature, layer) {
        layer.on('mouseover', function () {
            const DataItem = MonsoonData.find(item => item.ID === feature.properties.ID);

            if (feature.properties && feature.properties.ID) {
                let popupContent;
                const value = DataItem[selectedMapData.value] ? DataItem[selectedMapData.value].toFixed(2) : "NA"

                popupContent = `
                <div>
                ID: ${feature.properties.ID}<br/>
                STATE: ${feature.properties.STATE}<br/>
                DISTRICT: ${feature.properties.DISTRICT}<br/>
                TEHSIL: ${feature.properties.TEHSIL}<br/>
                VALUE: ${value}
                </div>
        `;

                layer.bindTooltip(popupContent, { sticky: true });
            }
            layer.openTooltip();
        });

        layer.on('mouseout', function () {
            layer.closeTooltip();
        });
    }



    const TalukaDensity = (density => {
        return density > 30 ? '#053062'
            : density > 20 ? '#2F7AB6'
                : density > 10 ? '#87BEDA'
                    : density > 0 ? '#DDEBF2'
                        : density > -10 ? '#FBE3D6'
                            : density > -20 ? '#F09C7A'
                                : density > -30 ? '#C13739'
                                    : '#68001F';
    })




    const TalukaStyle = (feature => {

        const getDensityFromSummerData = (ID) => {
            const DataItem = MonsoonData.find(item => item.ID === ID);
            return DataItem ? DataItem[selectedMapData.value] : null;
        };

        const density = getDensityFromSummerData(feature.properties.ID);

        return ({
            fillColor: TalukaDensity(density),
            weight: 0.5,
            opacity: 1,
            color: 'black',
            // dashArray: '2',
            fillOpacity: 1
        });
    });



    return (
        <>
            <div className='dasboard_page_container'>


                <div className='main_dashboard'>
                    <div className='left_panel'>

                        <div className="card_container" style={{ height: "100%", overflowY: "auto" }}>

                            <div className="accordion" >


                                <div className="accordion-item">
                                    <h2 className="accordion-header" id="panelsStayOpen-headingOne">
                                        <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#panelsStayOpen-collapseOne" aria-expanded="true" aria-controls="panelsStayOpen-collapseOne">
                                            Choose Data Layers
                                        </button>
                                    </h2>
                                    <div id="panelsStayOpen-collapseOne" className="accordion-collapse collapse show" aria-labelledby="panelsStayOpen-headingOne">

                                        <div className="accordion-body">
                                            <h6>Raster data</h6>
                                            {MonsoonDataOptions.slice(0, 1).map((option, index) => (
                                                <div key={index} className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="radio"
                                                        id={option.name}
                                                        value={option.value}
                                                        checked={selectedMapData && selectedMapData.name === option.name}
                                                        onChange={handleDataSelectionChange}
                                                    />
                                                    <label className="form-check-label" htmlFor={option.name}>{option.name}</label>



                                                </div>
                                            ))}

                                            <hr />
                                            <h6>Southwest monsoon (JJAS)</h6>
                                            {MonsoonDataOptions.slice(1, 6).map((option, index) => (
                                                <>

                                                    <div key={index} className="form-check">
                                                        <input
                                                            className="form-check-input"
                                                            type="radio"
                                                            id={option.name}
                                                            value={option.value}
                                                            checked={selectedMapData && selectedMapData.name === option.name}
                                                            onChange={handleDataSelectionChange}
                                                        />
                                                        <label className="form-check-label" htmlFor={option.name}>{option.name}</label>


                                                    </div>

                                                </>




                                            ))}

                                            <hr />
                                            <h6>Northeast monsoon (OND)</h6>
                                            {MonsoonDataOptions.slice(6, 11).map((option, index) => (
                                                <div key={index} className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="radio"
                                                        id={option.name}
                                                        value={option.value}
                                                        checked={selectedMapData && selectedMapData.name === option.name}
                                                        onChange={handleDataSelectionChange}
                                                    />
                                                    <label className="form-check-label" htmlFor={option.name}>{option.name}</label>

                                                </div>
                                            ))}





                                        </div>
                                    </div>
                                </div>

                                <div className="accordion-item">
                                    <h2 className="accordion-header" id="panelsStayOpen-headingThree">
                                        <button className="accordion-button" type="button" data-bs-toggle="collapse" data-bs-target="#panelsStayOpen-collapseThree" aria-expanded="true" aria-controls="panelsStayOpen-collapseThree">
                                            Base Map
                                        </button>
                                    </h2>
                                    <div id="panelsStayOpen-collapseThree" className="accordion-collapse collapse show" aria-labelledby="panelsStayOpen-headingThree">
                                        <div className="accordion-body">
                                            {BaseMapsLayers.map((option, index) => (
                                                <div key={index} className="form-check">
                                                    <input
                                                        type="radio"
                                                        id={option.name}
                                                        className="form-check-input"
                                                        value={option.name}
                                                        checked={selectedBasemapLayer.name === option.name}
                                                        onChange={handleBasemapSelection}
                                                    />
                                                    <label className="form-check-label" htmlFor={option.name}>{option.name}</label>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>


                                <div className="accordion-item">
                                    <h2 className="accordion-header" id="panelsStayOpen-headingTwo">
                                        <button className="accordion-button " type="button" data-bs-toggle="collapse" data-bs-target="#panelsStayOpen-collapseTwo" aria-expanded="frue" aria-controls="panelsStayOpen-collapseTwo">
                                            Choose Boundaries
                                        </button>
                                    </h2>
                                    <div id="panelsStayOpen-collapseTwo" className="accordion-collapse collapse show" aria-labelledby="panelsStayOpen-headingTwo">
                                        <div className="accordion-body">
                                            {VectorDataOptions.map(option => (
                                                <div key={option.value} className="form-check">
                                                    <input
                                                        className="form-check-input"
                                                        type="checkbox"
                                                        id={option.value}
                                                        value={option.value}
                                                        checked={selectedVectorData && selectedVectorData?.value === option.value}
                                                        onChange={handleVectorLayerSelection}
                                                    />
                                                    <label className="form-check-label" htmlFor={option.value}>{option.name}</label>

                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </div>

                            </div>


                        </div>


                    </div>

                    <div className='right_panel'>

                        <MapContainer
                            fullscreenControl={true}
                            center={mapCenter}
                            style={{ width: '100%', height: "100%", backgroundColor: 'white', border: 'none', margin: 'auto' }}
                            zoom={setInitialMapZoom()}
                            maxBounds={maxBounds}
                            // maxZoom={8}
                            minZoom={setInitialMapZoom()}
                            keyboard={false}
                            dragging={setDragging()}
                            // attributionControl={false}
                            // scrollWheelZoom={false}
                            doubleClickZoom={false}
                        >

                            <BaseMap />
                            <SearchBar />
                            <TileLayer
                                key={selectedBasemapLayer.url}
                                attribution={selectedBasemapLayer.attribution}
                                url={selectedBasemapLayer.url}
                                subdomains={selectedBasemapLayer.subdomains}
                            />


                            {selectedMapData && selectedMapData.value === "climate_zones_map" && (
                                <>
                                    <div className="legend_image_container" style={{ width: "200px" }}>
                                        <img
                                            src={climate_zones_legend}
                                            alt="Legend_Img"

                                        />
                                    </div>

                                    <ImageOverlay
                                        opacity={rasterLayerOpacity}
                                        attribution="Data Source: <a href='https://www.gloh2o.org/koppen/' target='_blank'>KÃ¶ppen-Geiger
                                Global climate classification (1991-2020)</a>"
                                        key="Irrigated_Rainfed"
                                        url={IndiaClimateZones}
                                        fitBounds={true}
                                        // interactive={true}
                                        bounds={[
                                            [6.7583333329999995, 97.4],
                                            [37.083333333, 68.125],
                                        ]}

                                    />
                                </>

                            )}



                            {selectedMapData && selectedMapData.value !== "climate_zones_map" && (
                                <>
                                    <div className="legend_panel_container">
                                        <MapLegend legendName={selectedMapData.name} />
                                    </div>
                                    <GeoJSON
                                        style={TalukaStyle}
                                        onEachFeature={TalukaOnEachfeature}
                                        data={IndiaSubdistricts}
                                    />
                                </>
                            )}



                            {selectedVectorData && (
                                <GeoJSON
                                    key={selectedVectorData.value}
                                    style={{
                                        fillColor: "black",
                                        weight: selectedVectorData.value === "SubDistrict_Boundary" ? 0.8 : 2,
                                        color: "black",
                                        fillOpacity: "0.001",
                                        interactive: false,
                                    }}
                                    data={selectedVectorData.data.features}
                                />

                            )}






                        </MapContainer>
                    </div>


                </div>
            </div>
            {/* <ShortFooter /> */}
        </>
    )
}

export default ClimateRiskDashboard
